.form-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 600px;
    margin: 130px auto;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-left: 5%;
    margin-right: 5%;
    padding: 10px;
  }
  
  .image-div {
    width: 50%;
    margin: 0;
  }
  
  .image-div img {
    margin: 0;
  }
  
  .login-form-container {
    width: 50%;
    padding: 20px;
    border-radius: 10px;
    background-color: #F7DED0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .form-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group input[type="text"],
  .form-group input[type="password"] {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  button {
    background-color: white;
    color: black;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    display: block;
    margin: 0 auto;
  }
  
  button:hover {
    background-color: #ffa500;
  }
  
  .form-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1000px; /* Adjusted maximum width */
    margin: 130px auto;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .image-div {
    width: 70%;
    height: 100%;
    margin: 0;
    background-size: cover;
    background-position: center;
  }
  
  .image-div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .login-form-container {
    width: 50%; /* Adjusted width */
    padding: 40px; /* Increased padding */
    border-radius: 10px;
    background-color: #F7DED0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .form-title {
    font-size: 32px; /* Increased font size */
    font-weight: bold;
    margin-bottom: 30px; /* Increased margin */
    text-align: center;
  }
  
  .form-group {
    margin-bottom: 30px; /* Increased margin */
  }
  
  .form-group input[type="text"],
  .form-group input[type="password"] {
    width: 100%;
    padding: 15px; /* Increased padding */
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .login-form-container button {
    background-color: white;
    color: black;
    padding: 15px 30px; /* Increased padding */
    border: none;
    font-weight: bolder;
    border-radius: 5px;
    cursor: pointer;
    display: block;
    margin: 0 auto;
    font-size: 1.5rem;
  }
  
  .login-form-container button:hover {
    background-color: #FFBE98;
  }
  .amatic-sc-regular {
    font-family: "Amatic SC", sans-serif;
    font-weight: 400;
    font-style: normal;
    background-color: white;
  }
  .error-message {
    color: black;
    margin-top: 5px;
    font-weight: bolder;
  }